* {
  font-family: 'Hind', sans-serif;
  font-family: 'Pangolin', cursive;
}

body {
  padding: 60px;
  margin: 0;
}

input {
  color: gray;
  font-size: 1.3rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;

}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 190px;
}